.header {
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 0.5vmin;
    font-size: 20px;
    height: 120px;
    max-width: 1500px;
    margin: auto;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color:hsl(0, 10%, 70%);
    user-select: none;
}

.header h1 {
    font-size: clamp(12px, 5vmin, 40px);
    transform: translateX(3.5em);
    flex-grow: 1;
    text-align: center;
}
nav {
    font-size: clamp(10px, 3vmin, 19px);
    
  
}
nav ul {
    display: flex;
    margin: 0 1em 0 0;padding: 0;
}

nav ul li {
    position: relative;
    margin-left: 1.5em;
    list-style: none;
    text-decoration: none;
    color: #4e6681;
    cursor: pointer;
}



nav ul li::after {
    content: "";
    position: absolute;
    display: block;
    height: 1px;
    width: 100%;
    background-color: #4e6681;
    bottom: -6px;
}

nav ul li:hover::after {
    background-color: #fff;
}
nav ul li:hover {
    color: #fff;
}

@media screen and (max-width: 1150px) {
    .header {
        height: 19vmin;
        flex-direction: column;
    }
    .header h1 {
        margin: 0.5em 0 0;
        transform: translateX(0);
        flex-grow: 0;
    }
    nav ul {
        
        margin: 2vmin 0 0 0  ;
    }

    nav {
        flex-grow: 1;
    }
    nav ul li::after {

        bottom: -2px;
    }
}

@media screen and (max-width: 250px) {
    nav {
        display: none;
    }
    .header h1 {
        margin: 6vmin;
        font-size: 5vmin;
    }
}

@media (hover: none) {
    nav ul li:hover::after {
        background-color: #4e6681;
    }
    nav ul li:hover {
        color: #4e6681;
    }
}
