.card-container {
    position: relative;
    display: flex;
    width: 400px;
    height: 500px;
    margin: 30px;

    user-select: none;
    flex-wrap: wrap;
    cursor:pointer;
    box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.5);
    transition: box-shadow 0.2s;
}
.card-container:hover {

    box-shadow: 8px 8px 4px rgba(0, 0, 0, 0.4);
    transition: box-shadow 0.1s, transform 0.1s;
    transform: translate(1px, 1px);
    
}
.card-container:active {
    box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.1s, transform 0.1s;
    transform: translate(3px, 3px);
}

.card-top {
    position: relative;
    width: 100%;
    height: 70%;
}
.card-bottom {
    display: flex;
    align-items: center;
    width: 100%;
    height: 30%;
    background-color: #ffffff;
}

.image {
    width: clamp(20px, 10vmin, 60px);
    margin: clamp(8px, 3vmin, 20px);
}

.card-text {
    flex-grow: 1;
    align-self: center;
    user-select: none;
    font-size: clamp(6px, 3vmin, 20px);
}

.card-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    transition: 0.1s ease-in-out;
    z-index: 5;
    opacity: 0;
}

.card-overlay:active {
    opacity: 1;
}

.back {
    position: absolute;
    height: 100%;
    width: 100%;
}

@media screen and (max-width: 500px) {
    .card-container {
        width: 75vmin;
        height: auto;
        margin: 5vmin auto 0;
        aspect-ratio: 1/1.2;
    }
}

@media screen and (max-width: 150px) {
    .card-text {
        font-size: clamp(4px, 3.5vmin, 8px);
    }
    .image {
        width: clamp(10px, 12vmin, 30px);
        margin: 5vmin;
    }
}

@media (hover:none){
    .card-container:hover {

        box-shadow: none;
        transition: none;
        transform:none;
        
    }
    .card-container:active {
        box-shadow: none;
        transition: none;
        transform:none;
    }

}
